html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Barlow Semi Condensed", sans-serif;
  font-size: 13px;
}
body.noscroll {
  overflow: hidden;
}

img {
  margin: 0;
  vertical-align: middle;
}

picture {
  margin: 0;
}

p {
  margin: 0;
}

a,
a:visited,
a:hover {
  text-decoration: none;
}

input,
textarea,
button,
select,
a {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

input:focus {
  outline: none;
}

address {
  font-style: normal;
}

blockquote {
  margin: 0;
}

.flex {
  display: flex;
}
.flex-jc-sb {
  justify-content: space-between;
}
.flex-jc-c {
  justify-content: center;
}
.flex-ai-c {
  align-items: center;
}
.flex-d-c {
  flex-direction: column;
}
.flex-d-r {
  flex-direction: row;
}

@media (max-width: 63.9375em) {
  .hide-for-mobile {
    display: none;
  }
}

@media (min-width: 64em) {
  .hide-for-desktop {
    display: none;
  }
}

.sr-only {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

.img-responsive {
  max-width: 100%;
  height: auto;
}

.attribution {
  color: hsl(263deg, 55%, 52%);
  font-size: 0.6875rem;
  text-align: center;
  background-color: hsl(210deg, 46%, 95%);
}

.attribution a {
  color: hsl(263deg, 55%, 52%);
}

.bg-color-violet {
  background-color: hsl(263deg, 55%, 52%);
}
.bg-color-gray {
  background-color: hsl(217deg, 19%, 35%);
}
.bg-color-darkblue {
  background-color: hsl(219deg, 29%, 14%);
}
.bg-color-white {
  background-color: hsl(0deg, 0%, 100%);
}

.tcolor-darkgray {
  color: hsl(219deg, 29%, 14%);
}
.tcolor-darkgray-50 {
  color: hsla(217deg, 19%, 35%, 0.5);
}
.tcolor-darkgray-70 {
  color: hsla(217deg, 19%, 35%, 0.7);
}
.tcolor-lightgray {
  color: hsl(0deg, 0%, 81%);
}
.tcolor-lightgray-50 {
  color: hsla(0deg, 0%, 81%, 0.5);
}
.tcolor-lightgray-70 {
  color: hsla(0deg, 0%, 81%, 0.7);
}
.tcolor-white {
  color: hsl(0deg, 0%, 100%);
}

.card-1 {
  grid-column: 1/3;
  grid-row: 1;
}
.card-2 {
  grid-column: 3;
  grid-row: 1;
}
.card-3 {
  grid-column: 1;
  grid-row: 2;
}
.card-4 {
  grid-column: 2/4;
  grid-row: 2;
}
.card-5 {
  grid-column: 4;
  grid-row: 1/3;
}

.testimonials {
  background-color: hsl(210deg, 46%, 95%);
  padding: 3.125rem 1.25rem;
}
@media (min-width: 64em) {
  .testimonials {
    display: grid;
    gap: 1.875rem;
    padding: 3.75rem;
  }
}
.testimonials blockquote {
  line-height: 1.7;
}
.testimonials .testimonial {
  border-radius: 0.6875rem;
  margin-bottom: 1.875rem;
}
@media (min-width: 64em) {
  .testimonials .testimonial {
    margin-bottom: 0;
  }
}
.testimonials .testimonial_container {
  padding: 1.5rem 2.25rem 2.0625rem 1.875rem;
}
.testimonials .testimonial .profile {
  display: flex;
  flex-direction: row;
  margin-bottom: 1.25rem;
}
.testimonials .testimonial .profile_img {
  width: 1.875rem;
  height: 1.875rem;
  border-radius: 50%;
  margin-right: 1rem;
  border: 1px solid hsl(0deg, 0%, 81%);
}
.testimonials .testimonial .profile .wrapper .author {
  font-size: 0.9375rem;
}
.testimonials .testimonial .title {
  font-size: 1.4375rem;
  margin-bottom: 1.25rem;
}
.testimonials .testimonial .text {
  width: 85%;
}

.card-1 {
  position: relative;
  background-image: url("/images/bg-pattern-quotation.svg");
  background-repeat: no-repeat;
  background-position: 85% 0%;
}