html {
    font-size: 100%;
    box-sizing: border-box;
}

*,
*::before,
*::after {
    box-sizing: inherit;
}

body {
    margin: 0;
    padding: 0;
    font-family: $fontFamilyBarlow;
    font-size: $fontSizeBody;
    
    &.noscroll {
        overflow: hidden;
    }

}

img {
    margin: 0;
    vertical-align: middle;
}

picture {
    margin: 0;
}

p {
    margin: 0;
}

a,
a:visited,
a:hover {
    text-decoration: none;
}

input,
textarea,
button,
select,
a {
  -webkit-tap-highlight-color: rgba(0,0,0,0);
}

h1,
h2,
h3,
h4,
h5,
h6 {
    margin: 0;
}

input:focus {
    outline: none;
}

address {
    font-style: normal;
}

blockquote {
    margin: 0;
}
// Flexbox
.flex {
    display: flex;

    &-jc-sb {
        justify-content: space-between;
    }

    &-jc-c {
        justify-content: center;
    }

    &-ai-c {
        align-items: center;
    }

    &-d-c {
        flex-direction: column;
    }

    &-d-r {
        flex-direction: row;
    }
}

.hide-for-mobile {
    @include breakpoint-down(medium) {
        display: none;
    }
}

.hide-for-desktop {
    @include breakpoint-up(large) {
        display: none;
    }
}

.sr-only {
    position: absolute;
    left: -10000px;
    top: auto;
    width: 1px;
    height: 1px;
    overflow: hidden;
}

.img-responsive {
    max-width: 100%;
    height: auto;

}

.attribution {
    color: $moderateViolet;
    font-size: rem(11);
    text-align: center;
    background-color: $lightGrayishBlue;
}

.attribution a {
    color: $moderateViolet;
}

.bg-color {

    &-violet {
        background-color: $moderateViolet;
    }
    
    &-gray {
        background-color: $veryDarkGrayishBlue;
    }

    &-darkblue {
        background-color: $veryDarkBlackishBlue;
    }

    &-white {
        background-color: $white;
    }
}

.tcolor {

    &-darkgray {
        color: $veryDarkBlackishBlue;

        &-50 {
            color: hsla(217, 19%, 35%, 0.5);
        }

        &-70 {
            color: hsla(217, 19%, 35%, 0.7);
        }
    }

    &-lightgray {
        color: $lightGray;

        &-50 {
            color: hsla(0, 0%, 81%, 0.5);

        }

        &-70 {
            color: hsla(0, 0%, 81%, 0.7);

        }
    }

    &-white {
        color: $white;
    }

}

.card {

    &-1 {
        grid-column: 1 / 3;
        grid-row: 1;
    }

    &-2 {
        grid-column: 3;
        grid-row: 1;
    }

    &-3 {
        grid-column: 1;
        grid-row: 2;
    }

    &-4 {
        grid-column: 2 / 4;
        grid-row: 2;
    }

    &-5 {
        grid-column: 4;
        grid-row: 1 / 3;
    }
}